
.main-menu ul {
  list-style: outside none none;
  margin: 0 -13px 0 0;
  padding: 0;
  text-align: right;
  li {
    display: inline-block;
    position: relative;
    a {
      color: #333;
      display: block;
      font-size: 14px;
      /*change menu distace top to left right*/
      padding: 26px 13px;
      /*change font weight*/
      font-weight: 500;
      text-transform: uppercase;
      transition: all 0.34s ease-in-out;
      &:hover {
        text-decoration: none; } } } }
.main-menu a:foucs {
  outline: none;
  text-decoration: none; }
.main-menu > ul > li.current-menu-item > a {
  color: $primary-color;
  /*change hover color*/ }
.main-menu > ul > li:hover > a {
  color: $primary-color;
  /*li dot hover change*/ }

