/*men item carve icon*/
.main-menu > ul > li.menu-item-has-children > a:after {
  content: "\f107 ";
  margin-left: 5px;
  font-family: "Fontawesome"; }

/*sub menu*/
.main-menu > ul > li.menu-item-has-children > ul.sub-menu {
  position: absolute;
  left: 0;
  top: 120%;
  background: #fff;
  z-index: 999;
  color: #333;
  text-align: left;
  min-width: 200px;
  transition: all 0.42s ease-in-out;
  border-top: 3px solid $primary-color;
  box-shadow: 0 2px 9px 0 rgba(102,102,102,0.1);
  @include hidden(); }
.main-menu > ul > li.menu-item-has-children:hover > ul.sub-menu {
  @include vis();
  top: 100%; }

.main-menu > ul > li.menu-item-has-children > ul.sub-menu > li,
.main-menu ul ul li {
  display: block; }

.main-menu > ul > li.menu-item-has-children > ul.sub-menu > li > a,
.main-menu ul ul li a {

  display: block;
  padding: 10px 15px;
  border-bottom: 1px solid #fafafa;
  text-transform: capitalize;
  @include afterparent(); }
.main-menu > ul > li.menu-item-has-children > ul.sub-menu > li:last-child > a,
.main-menu ul ul li:last-child a {
  border-bottom: 1px solid transparent; }
.main-menu > ul > li.menu-item-has-children > ul.sub-menu > li:hover > a,
.main-menu ul ul > li:hover > a {
  padding-left: 23px;
  color: $white-color;
  background: $primary-color; }
/* */// drop menu cart*/
.main-menu   ul ul li.menu-item-has-children > a:after {
  content: "\f105";
  position: absolute;
  font-family: "Fontawesome";
  right: 10px; }
.main-menu   ul ul li.menu-item-has-children.change-icon > a:after {
  content: "\f107"; }
.main-menu ul ul {
  position: absolute;
  left: 202px;
  top: 30px;
  background: #fff;
  z-index: 999;
  color: #333;
  text-align: left;
  min-width: 200px;
  transition: all 0.42s ease-in-out;
  border-top: 3px solid $primary-color;
  box-shadow: 0 2px 9px 0 rgba(102,102,102,0.1);
  @include hidden(); }
.main-menu > ul > li.menu-item-has-children > ul.sub-menu > li.menu-item-has-children:hover > ul.sub-menu,
.main-menu > ul > li.menu-item-has-children > ul.sub-menu > li.menu-item-has-children > ul.sub-menu > li.menu-item-has-children:hover > ul.sub-menu {
  top: 0px;
  @include vis(); }

.mobile-menu {
  display: none; }

.menu-click {
  background: $primary-color;
  border: 1px solid $primary-color;
  cursor: pointer;
  display: inline-block;
  position: absolute;
  right: 10px;
  padding: 8px 7px 4px;
  top: -46px;
  transition: all 0.34s $ease-in-out-back; }

.menu-click span {
  transition: all 0.34s $ease-in-out-back;
  width: 30px;
  background: $white-color;
  height: 2px;
  margin-bottom: 4px;
  display: block; }
//experintam mobile menu
.mobile-menu {
  display: none; }


