.site--slider__area {
  height: 800px;
  width: 100%; }

.single-slider {
  height: 800px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  width: 100%;

  @include afterparent();
  &:after {
    @include after(absolute, 0, 0, 100%, 100%);
    z-index: -1;
    background: #000;
    @include opacity(0.5); } }


.slide-content {
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  color: #fff;
  h2 {
    font-weight: 700;
    font-size: 72px;
    line-height: 72px;
    text-transform: uppercase; }

  p {
    font-size: 18px;
    font-weight: 400;
    margin: 0 auto 15px;
    width: 80%; }
  a {
    margin-right: 5px; } }
.slider-active {
  .owl-nav div {
    position: absolute;
    left: 0px;
    top: 50%;
    height: 40px;
    width: 40px;
    line-height: 40px;
    text-align: center;
    border-radius: 50%;
    font-size: 20px;
    color: #333;
    background: #fff;
    border: 1px solid #fff;
    transition: all 0.24s $ease-in-sine;
    @include hidden();
    &:hover {
      background: $secondary-color;
      border: 1px solid $secondary-color;
      color: #fff; } } }
.slider-active .owl-nav div.owl-next {
  left: auto;
  right: 0px; }
.site--slider__area:hover .slider-active .owl-nav div {
  @include vis();
  left: 30px; }
.site--slider__area:hover .slider-active .owl-nav div.owl-next {
  right: 30px;
  left: auto; }
.banner-arae {
  height: 300px;
  background-size: cover;
  background-position: bottom;
  background-attachment: fixed;
  background-repeat: no-repeat;
  @include afterparent();
  &:after {
    @include after(absolute, 0, 0, 100%, 100%);
    background: #000;
    z-index: -1;
    @include opacity(0.5); }
  h2 {
    color: #fff;
    text-transform: capitalize;
    font-size: 35px; } }
.bn_height {
  height: 300px; }
