
//after parent
@mixin afterparent() {
  position: relative;
  z-index: 1; }
// mixin for content after
@mixin after($absolute,$left,$top,$width,$height) {
  position: $absolute;
  left: $left;
  top: $top;
  width: $width;
  height: $height;
  content: ""; }
// mixin for after2
@mixin after2($absolute,$left,$bottom,$width,$height) {
  position: $absolute;
  left: $left;
  bottom: $bottom;
  width: $width;
  height: $height;
  content: ""; }

@mixin cover-img2($path,$bg,$bgp) {
  background-color: $bg;
  background-image: $path;
  background-size: cover;
  background-position: $bgp;
  background-repeat: no-repeat; }
@mixin ulol() {
  margin: 0;
  padding: 0;
  list-style: none; }
// text over flow
@mixin text-over() {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

@mixin hidden() {
  @include opacity(0);
  visibility: hidden; }
@mixin vis() {
  @include opacity(1);
  visibility: visible; }
@mixin vis-1($value-1) {
  @include opacity($value-1);
  visibility: visible; }

@mixin opacity($value) {
  $IEValue: $value*100;
  opacity: $value;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity="+$IEValue+")";
  filter: alpha(opacity=$IEValue); }
