
html,body {
  height: 100%; }
body {
  line-height: $line-height-2;
  font-size: $body-font-size;
  color: $body-font-colr;
  font-family: $body-font;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400; }


// heading
h1,h2,h3,h4,h5,h6 {
  margin: $heading-margin;
  padding: $heading-padding;
  font-family:  $heading-font; }
h1 {
  font-size: $h1-font-size; }
h2 {
  font-size: $h2-font-size; }
h3 {
  font-size: $h3-font-size; }
h4 {
  font-size: $h4-font-size; }
h5 {
  font-size: $h5-font-size; }
h6 {
  font-size: $h6-font-size; }
p {
  font-size: $default-font-size;
  margin: $p-margin;
  padding: $p-padding; }

a:hover {
  text-decoration: none; }

// theme default button
.btn-mr {
  display: inline-block;
  vertical-align: middle;
  font-size: 14px;
  padding: 12px 35px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: all 0.24s $ease-out-quint;
  line-height: 24px;
  cursor: pointer;
  font-weight: 500;
  &.pill {
    border-radius: 999px; } }




.th-primary {
  background: $primary-color;
  color: #fff;
  border: 1px solid $primary-color;
  border-radius: 3px;
  &:hover {
    background: shade($primary-color, 10%);
    border: 1px solid shade($primary-color, 10%);
    color: #fff; }
  &:focus {
    outline: none;
    color: #fff; } }
.th-secondary {
  background: $secondary-color;
  color: #fff;
  border: 1px solid $secondary-color;
  border-radius: 3px;
  &:hover {
    background: shade($secondary-color, 10%);
    border: 1px solid shade($secondary-color, 10%);
    color: #fff; }
  &:focus {
    outline: none;
    color: #fff; } }
.th-primary-outline {
  background: transparent;
  color: #333;
  border: 2px solid $primary-color;
  background: $primary-color;
  color: #fff;
  &:hover {
    background: transparent;

    color: #fff; }
  &:focus {
    outline: none;
    color: #fff; } }
.th-secondary-outline {
  background: transparent;
  color: #333;
  border: 2px solid $primary-color;
  background: $primary-color;
  color: #fff;
  &:hover {
    background: transparent;

    color: #333; }
  &:focus {
    outline: none;
    color: #fff; } }









// bootstrap button style
.btn {
  font-size: 16px;
  line-height: 24px;
  text-transform: capitalize;
  border-radius: 0px;
  padding: 8px 30px;
  font-weight: 400;
  transition: all 0.24s $ease-in-cubic;
  &.pill {
    border-radius: 999px; } }
.btn-primary {
  color: #fff !important;
  background: $button-primary;
  border-color: $button-primary;
  &:hover {
    background: shade($button-primary, 10%);
    border-color: shade($button-primary, 10%); } }
