img {
  max-width: 100%; }
// section tiitle setting
.section-ttile {
  margin-bottom: 60px;
  h2 {
    position: relative;
    font-size: 30px;
    text-transform: capitalize;
    display: block;
    font-weight: 700;
    margin: 0 0 0; }
  span {
    font-size: 60px;
    color: rgba(0,0,0, 0.1);
    font-weight: 100;
    text-transform: capitalize;
    position:  absolute;
    left: 50%;
    transform: translateX(-50%);
    top: -8px;
    display: inline-block; } }




