
// Large devices (desktops, 992px and up)
@media (min-width: 992px) and (max-width: 1199.98px) {

  
article.single-blog .blog-content .blog-footer ul{
    flex-wrap: wrap;

    li{
        flex: 0 0 50%;
    }
}
    


}

// Medium devices (tablets, 768px and up)
@media (min-width: 768px) and (max-width: 991.98px) {





    
 .site--slider__area:hover .slider-active .owl-nav div {
   
    left: 10px;
}   
    
  .site--slider__area:hover .slider-active .owl-nav div.owl-next {
    right: 10px;
    
} 
.slide-content h2{
    font-size: 40px;
    line-height: 40px;
}
    
.slide-content p{
    font-size: 16px;
    width: 100%;
}
    
    
  

}

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 767.98px) {



.site-header{
    padding: 15px 0;
}
    .site--slider__area{
        height: 450px;
    }
    
    .single-slider{
        height: 450px;
    }
    .slide-content h2 {
    font-size: 23px;
    line-height: 23px;
}
 
    .slide-content p {
    font-size: 14px;

    line-height: 24px;
}
    .slide-content a{
        margin: 5px 0;
    }
 .site--slider__area:hover .slider-active .owl-nav div {
   
    left: 10px;
}   
    
  .site--slider__area:hover .slider-active .owl-nav div.owl-next {
    right: 10px;
    
}  
    .section-ttile span{
        display: none;
    }
    
    
   article.single-blog .blog-content .blog-footer ul{
    flex-wrap: wrap;

    li{
        flex: 0 0 50%;
    }
}

 .product-carosel-active .owl-dots{
    bottom: -75px;
 }
 .footer-bottom{
    text-align: center;
 }   
.footer-bottom ul.f-social{
    text-align: center;
}

.section-ttile span{
    font-size: 40px;
    top: -15px;
}

article.single-blog{
     img{
        width: 100%;
    }
}

.banner-arae{
    height: 150px;
}
.bn_height{
    height: 150px;
}

.banner-arae h2{
    height: 20px;
}
.services-area{
    padding: 50px 0 20px;
}
.section-ttile{
    margin-bottom: 35px;
}

.blog-area{
    padding: 50px 0 20px;
}
.site-logo{
    img{
        height: 60px;
    }
}


}

// Small devices (landscape phones, 576px and up)
@media (min-width: 576px) and (max-width: 767.98px) {




 .section-ttile span{
        display: block;
    }
    





}