.services-area {
  padding: 100px 0 70px; }
.single-services {
  border: 1px solid #eee;
  padding: 15px;
  min-height: 230px;
  display: flex;
  border-radius: 3px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 30px;
  transition: all 0.24s ease-in-out;
  i.fa {
    font-size: 40px;
    margin-bottom: 15px;
    color: $primary-color; }
  h3 {
    text-transform: capitalize;
    font-size: 20px;
    margin: 0 0 15px; }
  p {
    margin: 0 0 0; }
  &:hover {
  	box-shadow: 0px 8px 14px 0px rgba(102, 102, 102, 0.15);
  	border: 1px solid transparent;
  	transform: translate(0, -5px); } }
