.site-footer {
	background: tint(#00325d, 30%);
	padding-top: 50px;
	color: #fff; }
.single-footer {
	margin-bottom: 30px;
	.footer-titile {
		text-transform: capitalize;
		margin: 0 0 20px;
		font-weight: 500;
		font-size: 25px; }
	ul.recnt-posts {
		margin: 0;
		padding: 0;
		list-style: none;
		li {
			display: block;
			a {
				display: block;
				color: #fff;
				padding: 5px 0;
				text-transform: capitalize;
				transition: all 0.24s ease-in-out;
				&:hover {
					color: $primary-color;
					padding-left: 5px; } } } } }
.single-footer .tags a {
	color: #fff;
	border: 2px solid #fff;
	display: inline-block;
	padding: 2px 10px;
	font-size: 14px;
	margin: 5px 2px;
	border-radius: 999px;
	transition: all 0.24s ease-in-out;
	text-transform: capitalize;
	font-weight: 300;
	&:hover {
		color: $primary-color;
		background: #fff; } }
.footer-bottom {
	padding: 10px 0;
	background: tint(#00325d, 15%);
	ul.f-social {
		margin: 0;
		padding: 0;
		list-style: none;
		text-align: right;
		li {
			display: inline-block;
			a {
				display: block;
				color: #fff;
				transition: all 0.24s ease-in-out;
				padding: 0 4px;
				&:hover {
					color: $primary-color; } } } } }


.footer-bottom p {
	margin: 0 0 0px; }
#scrollUp {
	position: fixed;
	right: 30px;
	bottom: 30px;
	background: $primary-color;
	color: #fff;
	width: 40px;
	height: 40px;
	line-height: 40px;
	text-align: center;
	border-radius: 50%; }
