.product-carosel-area {
  padding: 100px 0; }
.single-product {
	border: 1px solid #eee;
	padding: 10px; }
.product-carosel-active .owl-dots {

  position: absolute;
  left: 50%;
  bottom: -60px;
  transform: translateX(-50%);
  z-index: 999; }
.product-carosel-active .owl-dot {
  height: 15px;
  width: 15px;
  background: transparent;
  transition: all 0.24s ease-in-out;
  border: 2px solid $primary-color;
  display: inline-block;
  margin: 0 3px;
  border-radius: 50%; }

.product-carosel-active .owl-dot.active {
  background: $primary-color; }
