.blog-area {
	padding: 100px 0 70px;
	background: #f2f9ff;
	&.innerpage {
	  background: transparent; } }
article.single-blog {
	background: #fff;
	border-radius: 5px;
	box-shadow: 0px 8px 14px 0px rgba(102, 102, 102, 0.15);
	transition: all 0.24s ease-in-out;
	padding: 15px;
	margin-bottom: 30px;
	&.innerpage {
		box-shadow: none;
		border: 1px solid #eee; }
	.blog-content {
		h2 {
			font-size: 20px;
			text-transform: capitalize;
			line-height: 30px;
			margin: 0 0 15px;
			a {
				color: #00325d;
				transition: all 0.24s ease-in-out; } }
		.blog-footer {
			ul {
				margin: 0;
				padding: 0;
				list-style: none;
				display: flex;
				justify-content: space-between;
				li {

					a {
						font-size: 14px;
						display: block;
						padding: 0 2px;
						color: $primary-color;
						i.fa {
							padding-right: 3px; } } } } } } }
article.single-blog:hover {
  	.blog-content {
  		h2 {
  			a {
  				color: $primary-color; } } } }


article.single-blog:hover {
  box-shadow: 0px 25px 30px 0px rgba(102, 102, 102, 0.15); }

article.single-blog.innerpage:hover {
  box-shadow: 0px 8px 14px 0px rgba(102, 102, 102, 0.15);
  border: 1px solid transparent; }
article.single-blog.innerpage.Fullpage img {
	width: 100%; }

.custuom-single-card.card {
	border: none;
	box-shadow: 0px 8px 14px 0px rgba(102, 102, 102, 0.15);
	margin-bottom: 30px;
	.card-header {
		background: $secondary-color;
		color: #fff;
		border-bottom: 1px solid transparent;
		font-size: 20px;
		font-weight: 500;
		text-transform: uppercase; }
	.card-body {
		ul {
			margin: 0;
			padding: 0;
			list-style: none;
			li {
				display: block;
				a {
					display: block;
					color: #00325d;
					text-transform: capitalize;
					padding: 5px 0;
					border-bottom: 1px dashed  #eee;
					transition: all 0.24s ease-in-out;
					&:hover {
						color: $primary-color;
						padding-left: 5px; } }
				&:last-child a {
					padding-bottom: 0px;
					border-bottom: 1px solid transparent; } } } } }

